import { useState } from 'react';
import '../style-sheets/Side.css';
import HallOfFame from './HallOfFame';
import seeMore from '../imagenes/seemore2.png';
import seeLess from '../imagenes/seeless2.png';
import paypal from '../imagenes/paypal.png'
import close from '../imagenes/closebutton2.png'

    /*
    import principios from '../imagenes/foco.png';
    import copyleft from '../imagenes/credits.png';
    import heart from '../imagenes/heart.png';
    import hide from '../imagenes/close.png'
    import star from '../imagenes/star.png'
    */

const Side = (props) => {
    const [sideActive,setSideActive]= useState(false)
    const [principles, setPrinciples] = useState(false)
    const [support, setSupport] = useState(false)
    const [credits, setCredits] = useState(false)
    const [hallOfFame, setHallOfFame] = useState(false)

    const turnOn = ()=> {
        setSideActive(true)
    }

    const turnOff = () => {
        setTimeout(() => {
          setSideActive(false);
        }, 20); // Espera 10 milisegundos antes de ejecutar el cdigo
      };
      
      function handleSupport() {
        setPrinciples(false);
        setCredits(false);
        setHallOfFame(false);
        setSupport(true)
      }

      function handlePrinciples () {
        setCredits(false);
        setHallOfFame(false);
        setSupport(false);
        setPrinciples(true);
      }

      function handleCredits() {
        setHallOfFame(false);
        setSupport(false);
        setPrinciples(false);
        setCredits(true);
      }
      
      function handleHallOfFame() {
        setSupport(false);
        setPrinciples(false);
        setCredits(false);
        setHallOfFame(true);
      }

      function toggleButton(setter) {
        setter(false)
      }

return (
    <>
    <div onClick={turnOn} className={'sideMenu' + (sideActive? '-expanded': '')} >
        {!sideActive? 
         <img src={seeMore} alt="more" />
        :
        <>
        <div className='hide' onClick={turnOff}>
            <img src={seeLess} alt="less" />
        </div>
        
        <div className='options'>
            <div className='item' onClick={handleHallOfFame}> 
                <p>Saln de la fama</p>
            </div>
            <div className='item' onClick={handleSupport}> 
                 <p>Contribuir</p>
            </div>
            <div className='item' onClick={handlePrinciples}> 
                <p>Principios</p>
            </div>
            <div className='item' onClick={handleCredits}> 
                 <p>Crditos</p>
            </div>
            <div className='item' onClick={turnOff}> 
                <p>Ocultar</p>
            </div>
        </div>

        </>        
        }
    </div>
    
    {hallOfFame? 
        <> 
            <HallOfFame hallOfFameArray={props.hallOfFameArray}
                        setHallOfFame={setHallOfFame}
                        normalVersion={props.normalVersion} setNormalVersion={props.setNormalVersion}/>
        </>
    :
    null

    }

    {support? 
       ( <div className='extra-containers'>
         <img className='closeButton' src={close} alt='close' onClick={()=> toggleButton(setSupport)}/>
            <p id="support-content">
                <i>Terraplanistas del mundo, mochaos!</i><br/><br/>Si te gusta el contenido de nuestro sitio, puedes contribuir a que siga funcionando con un donativo a travs de PayPal.<br/>Gracias!
            </p>
            <div className='donateImages'>
            <a href="https://www.paypal.com/donate/?hosted_button_id=F39SHBQC8GX9S" rel='noreferrer' target="_blank">
            <img src={paypal} alt="paypal" className="paypal" /> </a> 
             {//<img src={buyme} alt='buyme' className='buyme'/>
             }
            </div>    
        </div> )
    :
    null
    }

    {principles?
        (
        <div className='extra-containers'>
            <img className='closeButton' onClick={()=> toggleButton(setPrinciples)}
                src={close} alt='close'/>
                <ul className="principles-content">
                    <li>
                        Los aqu reunidos nos rebelamos contra la tirana del silogismo y el opresivo yugo del razonamiento.
                    </li>
                    <li>
                        El rigor es el becerro de oro al que se postran quienes pierden el tiempo preguntndole a los hechos su opinin.
                    </li>
                    <li>
                        La metfora espectacular, la comparacin temeraria, son el relmpago que libera al pensamiento de la crcel del sentido de la proporcin.
                    </li>
                    <li>
                        Donde hay voluntad hay causalidad.
                    </li>
                    <li>
                        El <em>non sequitur</em> es la supersticin que inventaron quienes no saben expresarse enrgicamente.
                    </li> 
                </ul> 
            <p> Bienvenido a <i>El Terraplanista: <br/>La casa del voluntarismo argumentativo</i></p>
        </div>
        )
        :
        null
    }

    {credits?
        (
        <div className='extra-containers'>
            <img className='closeButton' onClick={()=> toggleButton(setCredits)}
                src={close} alt='close'/>
            <p>Todas las declaraciones son mrito exclusivo de sus autores y presumiblemente fueron emitidas sin que mediara coaccin alguna.</p>
            <p>Respetamos las faltas ortogrficas y otras formas de disidencia idiomtica de las fuentes originales.</p>
            <p>Logotipo: cortesa de Monero Hernndez</p>
            <p>Los ociosos tras esta iniciativa:</p>
            <p>
                <a href="https://twitter.com/suaste86" style={{
                    color: 'blue',
                    textDecoration: 'none'
                    }}>@suaste86</a> {''}
                <a href="https://twitter.com/tesiture" style={{
                    color: 'blue',
                    textDecoration: 'none'
                    }}>@tesiture</a>
            </p>
        </div>
        )
        :
        null
    }

    </>
)
}

export default Side;